import React, { useState, useEffect } from 'react';
import Header from './../header';

function Count(props) {
    const [count, setCount] = useState(0);
    const [error, setError] = useState(false);
    useEffect(()=>{
        fetch("https://api.countapi.xyz/update/mac-github/?amount=1")
        .then(res => res.json())
        .then(
            (result) => {
                setCount(result.value);
            },
            (error) => {
                setError({error});
            }
        )
    },[]);
    return (
        <>
            <Header number={props.number} />
            <div className="App-container" style={{'backgroundColor':'#52006A', 'minHeight':'calc(100vh)', 'display':'flex', 'alignItems': 'center', 'justifyContent':'center'}}>
                <div className="meal-header" style={{'color': '#CD113B'}}>
                    <h3>This page was viewed</h3>
                    <h1>{count}</h1>
                    <h3>times</h3>
                </div>
            </div>
        </>
    );
}

export default Count;
